.toolbar {
  width: 100%;
  position: fixed;
  height: 26px;
  background-color: white;
  border-bottom: 1px solid gray;
  z-index: 100;
  display: flex;
  align-items: center; }
  .toolbar .toggle-panel-icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    cursor: pointer;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    margin: auto 3px auto;
    text-align: center; }
  .toolbar .icon-half {
    background-image: url("../../images/svg/half.svg"); }
  .toolbar .icon-rhalf {
    background-image: url("../../images/svg/rhalf.svg"); }
  .toolbar .icon-lhalf {
    background-image: url("../../images/svg/lhalf.svg"); }
  .toolbar .h1, .toolbar .h2, .toolbar .h3, .toolbar .h4, .toolbar .h5, .toolbar .h6 {
    font-weight: bold; }
